<template>  
    <section id="divMainAssetGroupListingData" class="main-content no-left-bar"> 
        <loader :is-visible="isLoading"></loader>  
        <div class="container-fluid padding-t_10 p-0">
            <div class="accordion custom-accr" id="accordionExample">
                <div class="card">
                    <div class="col-md-12 p-0">
                <div class="theme-primary partition">
                    <span class="p-name text-white">Missing Product</span>
                </div>
                 </div>
                   
                    <div class="table-responsive" >
                    <table class="table table-bordered mt-0 mb-0 nowrap" id="itmgrplisting">
                        <thead class="thead-bg">
                            <tr>
                                <th style="width:50px !important;" class="text-center" ></th>
                                <th><span href="javascript:;"  id="TAG">Location</span></th>
                                <th><span href="javascript:;" >product Type </span></th>
                                <th><span href="javascript:;" >Product Name</span></th>
                                <th ><span  id="group_name" >Unique Code </span></th>
                                <th><span  id="ReadTime" >Status </span></th>
                                <!-- <th class="text-center"><span  class="sort"  @click="sortdata($event)" id="AttachedItems">Attached Items </span></th> -->
                                
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,index) in GroupData" :key="index">
                                <td class="text-center">
                                    <input class="chkItems" :value="item.group_id" type="checkbox">
                                </td>
                                
                                <td>
                                    <span class="text-overflow-dynamic-container">{{ item.EPC_TAG }}</span>
                                </td>
                                <td>
                                    <span class="text-overflow-dynamic-container">{{ item.Location }}</span>
                                </td>
                                
                                <td>
                                    <span class="text-overflow-dynamic-container">{{item.productType}}</span>
                                </td>
                                <td>
                                    <span class="text-overflow-dynamic-container">{{item.ProductName}}</span>
                                </td>
                                <td>
                                    <span class="text-overflow-dynamic-container">{{item.UniqueCode}}</span>
                                </td>
                                <td>
                                    <span class="text-overflow-dynamic-container">{{item.Status}}</span>
                                </td>
                                <!-- <td>
                                    <span class="text-overflow-dynamic-container">{{item.AttachedItems  }}</span>
                                </td> -->
                                <td class="text-center single-action">
                                    <span class="text-dark"><em class="fa fa-briefcase" aria-hidden="true"></em><span class="noti-circle noti-blue">{{item.TotalItemsCount}}</span></span>
                                </td>
                              
                            </tr>
                            
                            <tr v-if="noRecord">
                                <td colspan="8" class="text-center text-danger no-record">{{$t('NoRecord(s)found')}}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                </div>
            </div>
        </div>  
        <div class="right-content">          
            


                
                <Pager  v-bind:numberofrecords="TotalRecords" :CurrentPageNumber="CurrentPage" prev-text="Prev" first-text="First" last-text="Last" next-text="Next"  v-on:SetCurrentPage="pagerMethod"></Pager>
        </div>
    </section>

</template>
<script>
import DataService from '../../service/DataService';
import Pager from '../../components/Common/Pager.vue';
import StatusList from '../../components/Common/ListingStatus.vue'
export default{
    components:{
        Pager,StatusList
    },
    data(){
        return{
            txtsearch:null,
            Case:0,
            ListType1:true,
            ePC_TAG:'',
            Location:'',
            productType:'',
            ProductName:'',
            UniqueCode:'',
            locationId:0,
            Status:'',
            sortBy:'',
            sortExp:'',
            search:'',
            Name:'',
            GroupData:[],
            noRecord:false,
            TotalRecords: 0,
            pageNum: 1,
            pageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            isLoading:false
            
                
        }
    },
    created:function(){ 
        this.GetMissingList();
    },
    methods:{
        GetMissingList:function(){  
            var vueObj=this;
            var url =`search=${vueObj.search}&locationId=${vueObj.locationId}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&ePC_TAG=${vueObj.ePC_TAG}`;                                                         
            DataService.GetRFIDMissingAssets(url).then(response=>{  
                if(response.data !=null){
                    
                    if(response.data.length>0){                                                
                        vueObj.GroupData=response.data;
                        vueObj.TotalRecords = response.data[0].TOTAL
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.noRecord=false;
                        vueObj.isLoading=false;
                        
                    }
                    else{
                        vueObj.GroupData=[];
                        vueObj.noRecord=true;
                        vueObj.TotalRecords=0;
                    }
                }
                else{
                    vueObj.noRecord = true;
                    vueObj.GroupData = [];                   
                    vueObj.TotalRecords = 0
                    vueObj.isLoading=false;
                }   
                setTimeout(function(){vueObj.CheckBoxBootstrap()}, 100);        
            });
        },
        SearchData:function(){    
             this.pageNum=1;         
            this.GetMissingList();
        },
        ResetData:function(){
            this.EPC_TAG='';
            this.GetMissingList()
        },
        pagerMethod: function (value) {
            
            this.PageSize = value.PageSize;
            this.pageNum =value.PageNumber;
            this.GetMissingList();
        },

        sortdata: function ($event) {
            ;
         console.log('event',$event)
           this.sortBy = $event.currentTarget.id;
           this.sortExp=(this.sortExp=='DESC')?'ASC':'DESC'
           this.GetMissingList();
        },
       
        DeleteRFID:function(){
            var vm = this;
            var val = "";
        $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
            if (val.length > 0)            
                val += ',';
                val += $(this).val();                      
        });      
       
        if (val.length > 0) {
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group_Item")), function () {             
                var postJSON = {
                    ids: val,
                    tblname: "Talygen_IoT_Items_Group",
                    primarycolname: "Group_Id"
                };
                vm.isLoading=true;
                var poststing=JSON.stringify(postJSON);
                DataService.DeleteGroupList(poststing).then(response=>{
                    if(response.data){
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Group_Item')]), "Success", true, vm.$t('Alert'));
                        vm.GetGroupList();
                    }
                    else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Item')]), "failure", true, vm.$t('Alert'));
                    }
                });
            });
        }
        },
       
    }
}
</script>